
.lists{width:100%;overflow:scroll
}
.lists ul{list-style-type:none;margin:0;padding:0
}
.lists ul li{width:100%;height:auto;margin:5px 0;border:1px solid rgba(2,12,15,0.89);position:relative
}
.lists ul li figure{margin:0
}
.lists ul li figure img{display:inline-block;width:30%;height:auto;margin:5px 2px 3px 3px;vertical-align:top
}
.lists ul li figure figcaption{display:inline-block;width:60%;font-size:14px
}
.lists ul li figure figcaption h2{font-size:.8em
}
.lists ul li button{position:absolute;right:10px;bottom:6px;border-radius:5px;background-color:#f7edd9
}


