
.seller{position:absolute;top:175px;width:100%;left:0;bottom:0;overflow:scroll
}
.seller .selle_top{height:150px;border-bottom:1px solid rgba(7,17,27,0.1);padding:0 18px;box-sizing:border-box;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
.seller .selle_top>div{-ms-flex:1;flex:1
}
.seller .selle_top .name{border-bottom:1px solid rgba(7,17,27,0.1);display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center
}
.seller .selle_top .name>p{font-size:14px;color:#07111b;margin-bottom:8px
}
.seller .selle_top .name>div{font-size:10px;color:#4d555d
}
.seller .selle_top .name>div img{width:12px;height:12px;margin-right:6px
}
.seller .selle_top .name>div span{margin:0 12px 0 2px
}
.seller .selle_top .data{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.seller .selle_top .data>div{-ms-flex:1;flex:1;text-align:center;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;-ms-flex-direction:column;flex-direction:column;border-right:1px solid rgba(7,17,27,0.1)
}
.seller .selle_top .data>div:nth-child(3){border-right:none
}
.seller .selle_top .data>div p:nth-child(1){font-size:10px;color:#93999f;margin-bottom:4px
}
.seller .selle_top .data>div p:nth-child(2){font-size:24px;font-weight:200;color:#07111b
}
.seller .selle_top .data>div p:nth-child(2) span{font-size:10px
}
.seller .layer{height:16px;background:#f3f5f7;border-bottom:1px solid #e6e7e8
}
.seller .layer2{height:16px;background:#f3f5f7;border-bottom:1px solid #e6e7e8;border-top:1px solid #e6e7e8
}
.seller .selle_bottom{padding:18px
}
.seller .selle_bottom .title{font-size:14px;color:#07111b;margin-bottom:8px
}
.seller .selle_bottom .content{margin:0 12px 16px;font-size:12px;font-weight:200;color:#f01414;line-height:24px
}
.seller .selle_bottom .box{height:48px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;border-top:1px solid #e6e7e8;padding-left:12px
}
.seller .selle_bottom .box img{width:16px;height:16px;margin-right:6px
}
.seller .selle_bottom .box span{font-size:12px;font-weight:200;color:#07111b
}
.seller .live{padding:18px
}
.seller .live .title{font-size:14px;color:#07111b;margin-bottom:12px
}
.seller .live ul{height:90px;width:504px
}
.seller .live ul li{margin-right:6px;width:120px;height:90px;float:left
}
.seller .live ul li img{width:120px;height:90px
}
.seller .xinxi{padding:18px
}
.seller .xinxi .title{font-size:14px;color:#07111b;margin-bottom:12px
}
.uldiv{overflow-x:scroll
}
.seller .xinxi div{height:48px;line-height:16px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;padding-left:12px;border-top:1px solid rgba(7,17,27,0.1);font-size:12px;font-weight:200;color:#07111b
}


