
.foods{width:100%;height:auto
}
.foods .left{list-style:none;margin:0;padding:0;width:20%;border:1px solid gray;height:385px;display:inline-block
}
.foods .left li{display:table;width:100%;border-bottom:1px solid #dad9d9
}
.foods .left li p{width:100%;height:45px;line-height:45px;text-align:center;background-color:#f3f1f1
}
.foods .right{list-style:none;margin:0;padding:0;width:75%;border:1px solid greenyellow;margin-left:2%;height:385px;display:inline-block
}
.foods .right li{display:table;width:100%;height:125px;border-bottom:1px solid darkgray
}
.foods .right li figure{margin:0
}
.foods .right li figure img{width:20%;height:auto;margin:0
}
.foods .right li figure figcaption{display:inline-block
}
.foods .right li figure figcaption h3{margin:0;font-size:14px
}
.shopping-cart{height:48px;background:#141d27;position:fixed;bottom:0;left:0;width:100%;line-height:48px
}
.shopping-cart .box{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
.shopping-cart .car_img{width:56px;height:56px;border-radius:50%;background:#141d27;position:absolute;left:12px;top:-10px
}
.shopping-cart .car_img div{width:44px;height:44px;background:#2b333b;border-radius:50%;margin:6px 0 0 6px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.shopping-cart .car_img div img{width:20px;height:20px
}
.buy_btn{position:absolute;right:0;top:0;width:105px;height:48px;text-align:center;line-height:48px;color:rgba(255,255,255,0.4);background:#2b333b;font-size:12px
}
.shopping-cart .total_price{font-size:16px;color:rgba(255,255,255,0.4);font-weight:700;margin-left:80px
}
.shopping-cart .border{padding:24px 0 0 1px;background:rgba(255,255,255,0.1);margin:0 12px
}
.shopping-cart .instructions{font-size:10px;color:rgba(255,255,255,0.4)
}


