
.lists{width:100%;overflow:scroll
}
.lists img{display:inline-block;width:100%;height:185px;margin:5px 2px 3px 3px
}
.selle_bottom{padding:18px
}
.selle_bottom .title{font-size:14px;color:#07111b;margin-bottom:8px
}
.selle_bottom .content{margin:0 12px 16px;font-size:12px;font-weight:200;color:#f01414;line-height:24px
}
.selle_bottom .box{height:48px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;border-top:1px solid #e6e7e8;padding-left:12px
}
.selle_bottom .box img{width:16px;height:16px;margin-right:6px
}
.selle_bottom .box span{font-size:12px;font-weight:200;color:#07111b
}
.merchant-name{padding:0 18px;padding-bottom:18px;border-bottom:1px solid darkgray
}
.merchant-name h3{display:inline-block;width:100%
}
.merchant-name div{display:inline-block;width:75%
}
.merchant-name div img{width:20px;height:auto;vertical-align:text-top
}
.merchant-name div span{height:20px;display:inline-block;margin-left:5px;line-height:20px
}
.merchant-name button{border-radius:5px;background-color:orange;border:none;padding:5px;color:white
}
.xinxi{padding:18px
}
.xinxi .title{font-size:14px;color:#07111b;margin-bottom:12px
}
.xinxi div{height:auto;-ms-flex-align:center;align-items:center;padding-left:12px;border-top:1px solid rgba(7,17,27,0.1);font-size:12px;font-weight:200;color:#07111b
}
.xinxi div p{display:block;width:100%;margin:0;line-height:35px
}


